/*CSS Document */
body,p,ul,ol,dl,dd,h1,h2,h3,h4,h5,h6,td,input,select,option,textarea{
  margin:0;
  padding:0;
}
*{
  padding:0;
  margin:0;
}
html,body{
  width: 100%;
  height: 100%;
}
body{
  font-family:"\5FAE\8F6F\96C5\9ED1";
}
img{
  display: block;
  border:none;
}
input{
  outline:none;
  border:none;
}
ul{
  list-style: none;
}
a{
  text-decoration: none;
}
/*----------------------------header------------------*/
.basicTable{
  color:#888888;
  padding:0 2%;
  font-size:10px;
}
.BasicTableHeaderBox{
  width: 100%;
  height: 100px;
}
.BasicTableHeaderTitle{
  width: 100%;
  height: 60px;
  font-size:20px;
  line-height:60px;
}
.BasicTableHeaderTitle h3{
  font-weight:100;
}
.BasicTableHeaderTabBox{
  width: 100%;
  height: 40px;
  float: left;
}
.BasicTableHeaderTab{
  width: 70%;
  height: 40px;
  font-size:10px;
  line-height:40px;
  background-color:#f5f5f5;
  float: left;
  text-indent: 1%;
}
.BasicTableHeaderTab h5{
  font-weight:100;
  font-size: 12px;
}
.BasicTableHeaderTabSearchBox{
  width: 28.5%;
  height: 40px;
  float: left;
  margin-left: 1%;
}
.BasicTableHeaderTabSearchInput{
  width: 88%;
  height: 40px;
  background-color:#e5e5e5;
  float: left;
}
.BasicTableHeaderTabSearchSpan{
  width: 12%;
  height: 40px;
  font-size: 10px;
  font-weight:100;
  line-height:40px;
  background-color:#4f4f4f;
  float: left;
}
.BasicTableHeaderTabSearchSpan{
  text-align:center;
}
.BasicTableHeaderTabSearchSpan .iconfont{
  color:#fff;
  font-size:14px;
}
/*-----------------------list----------------*/
.BasicTableListBox .BasicTableList:nth-child(2n+1){
  margin-right:2%;
}
.BasicTableList{
  width: 48.8%;
  /*height: 215px;*/
  margin-top:18px;
  /*border:1px solid #9d4a9c;*/
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  float: left;
  /*margin-right:2%;*/
}
.BasicTableListTitle{
  width: 100%;
  height: 37px;
  line-height: 37px;
}
.BasicTableListTitle .iconfont{
  color: #fff;
  vertical-align: middle;
  margin: 0 6px 0 16px;
}
.BasicTableListTitleSpan{
  color:#fff;
}
.BasicTableListTable{
  width: 97%;
  margin: 2.8% auto;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.BasicTableListIconfont{
  float: right;
}
.BasicTableListIconfont .iconfont{
  margin: 0 6px 0px 6px;
  font-size:14px;
}
.BasicTableListTable td{
  height: 36px;
  line-height: 36px;
  border-top:1px solid #dddddd;
}
.BasicTableListTable tr:nth-child(1){
  font-weight: 900;
}
.BasicTableListTable tr:nth-child(1) td{
  border-top:0;
}
.BasicTableListTable tr td:nth-child(1){
  display: inline-block;
  width: 8.3%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableListTable tr td:nth-child(2){
  display: inline-block;
  width: 27.8%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableListTable tr td:nth-child(3){
  display: inline-block;
  width: 29.4%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableListTable tr td:nth-child(4){
  display: inline-block;
  width: 32.9%;
  text-align: left;
  text-indent: 2%;
}
/*------------------------------sum------------------------*/
.BasicTableSun{
  width: 100%;
}
.BasicTableSunTable tr:nth-child(1){
  background-color: #ddd;
}
.BasicTableSunTable tr td:nth-child(1){
  display: inline-block;
  width: 17%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableSunTable tr td:nth-child(2){
  display: inline-block;
  width: 28.96%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableSunTable tr td:nth-child(3){
  display: inline-block;
  width: 14.5%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableSunTable tr td:nth-child(4){
  display: inline-block;
  width: 13.4%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableSunTable tr td:nth-child(5){
  display: inline-block;
  width: 25.7%;
  text-align: left;
  text-indent: 2%;
}
.BasicTableSunTable .iconfont{
  vertical-align: middle;
  margin-right:4px;
}
.BasicTableSunTable img{
  display: inline-block;
  /*vertical-align:middle;*/
  margin-right:4px;
}
.BasicTableSunTable {
  border:1px solid #dddddd;
  border-collapse:collapse;
}
.BasicTableSunTable tr:not(:first-child) td:nth-child(4) span{
  padding:7px 6px;
  color:#fff;
}
.BasicTableSunTable .BasicTableImg img{
  vertical-align: middle;
}
